@import 'main';
// End Of Import

// Start Header




/*
=====
LODEARS
=====
*/
html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #cc9430;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  

 
.owl-carousel .owl-item img {
    width: auto;
}








/*------ navbar start ------*/
.nav-bar {
  background-color: #03224f;
  padding: 20px 0;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .icons {
    width: 220px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .menu, .search, .user {
      font-size: 24px;
      color: rgb(255, 255, 255);
    }
    .menu, .search {
      display: none;
    }
    .user {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
    .log-in {
      font-family: ar-light;
      font-size: 18px;
      color: #fff;
      padding: 15px 0 0 0;
      .log-in-anchor {
        transition: all .3s ease-in-out;
        &:hover {
          color: #03224f;
        }
      }
      span {
        padding: 0 2px;
      }
    }
  }
  
}

/*------ navbar end ------*/



/*------ navlist start ------*/
.nav-list {
  background-color: rgb(242, 242, 242);
  padding: 9px 0;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .nav-list-links {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
      font-family: ar-md;
      font-size: 17px;
      color: #03224f;
      margin: 0 0 0 14px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #000;
      }
    }
  }
  .contact-icons {
    .whats-app, .phone, .email {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      color: #fff;
      font-size: 18px;
      transition: all .5s ease-in-out;
      &:hover {
        background-color: #03224f;
        background-image: linear-gradient( 0deg, #03224f 0%, #03224f 100%);
      }
    }
    .whats-app {
      background-color: rgb(13, 193, 67);
    }
    .phone {
      margin: 0 5px;
      transform: rotateY(180deg);
      background-image: linear-gradient( 270deg, rgb(199,200,200) 0%, rgb(156,159,166) 100%);
    }
    .email {
      background-image: linear-gradient( 90deg, rgb(199,200,200) 0%, rgb(156,159,166) 100%);
    }
  }
}
/*------ navlist end ------*/






/*------ section one start ------*/
.section-one {
  margin: 30px 0 0 0;
  .sections {
    border-style: solid;
    border-width: 2px;
    border-color: rgb(237, 237, 237);
    border-radius: 23px;
    padding: 25px 20px 15px 20px;
    margin: 0 0 15px 0;
    .sections-header {
      margin: 0 0 10px 0;
      font-family: ar-bold;
      font-size: 18px;
      color: rgb(3, 34, 79);
    }
    .main-menu {
      .sub-menu {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        cursor: pointer;
        border-bottom: 1px solid rgb(230, 230, 230);
        padding: 10px 13px;
        transition: all .3s ease-in-out;
        &:last-child {
          border: none;
        }
        img {
          transition: all .3s ease-in-out;
        }
        span {
          font-family: ar-reg;
          font-size: 16px;
          color: #000;
          padding: 0 15px;
        }
        &::after {
          content: "\f0d9"; /* FontAwesome Unicode */
          font-family: 'Font Awesome\ 5 Free';
          font-weight: 600;
          font-size: 14px;
          color: #000;
          opacity: 0.5;
          position: absolute;
          top: 50%;
          left: 14px;
          transform: translateY(-50%);
          transition: all .3s ease-in-out;
        }
        &:hover {
          .icon-one {
            content: url("../images/icon-c-1.png");
          }
          .icon-two {
            content: url("../images/icon-c-2.png");
          }
          .icon-three {
            content: url("../images/icon-c-3.png");
          }
          .icon-four {
            content: url("../images/icon-c-4.png");
          }
          span, &::after {
            color: #03224f;
            opacity: 1;
          }
          .sub-menu-hover {
            opacity: 1;
            visibility: visible;
            left: -50%;
          }
        }
      }
      .sub-menu-hover {
        opacity: 0;
        visibility: hidden;
        min-width: 197px;
        min-height: 219px;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        background-color: rgb(255, 255, 255);
        border-radius: 23px;
        padding: 20px 30px;
        transition: all .5s ease-in-out;
        position: absolute;
        top: -5%;
        left: -55%;
        z-index: 9;
        li {
          font-family: ar-reg;
          font-size: 15px;
          color: #999;
          transition: all .3s ease-in-out;
          margin: 0 0 10px 0;
          &:hover {
            color: #03224f;
          }
        }
      }
    }
  }
  .main-slider {
    .main-slider-img {
      position: relative;
      overflow: hidden;
      img {
        transition: all .5s ease-in-out;
      }
      &:hover {
        img {
          transform: scale(1.02);
        }
      }
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        .owl-prev, .owl-next {
          outline: none;
          width: 40px;
          height: 40px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 12px;
          background-color: rgba(0, 0, 0, 0.502);
          border-radius: 50%;
          color: rgb(255, 255, 255);
          transition: all .5s ease-in-out;
          &:hover {
            color: #03224f;
            background-color: #fff;
          }
        }
        .owl-prev {
          right: 10px;
        }
        .owl-next {
          left: 10px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: rgba(0, 0, 0, 0.502);
          border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: #fff;
          }
        }
      }
    }
    .btns {
      margin: 25px 0 0 0;
      display: flex;
      justify-content: center;
      align-items: center;
      .slider-btn {
        width: 335px;
        height: 57px;
        background-color: rgb(3, 34, 79);
        border-radius: 50px;
        font-family: ar-bold;
        font-size: 20px;
        color: rgb(255, 255, 255);
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 0 0 20px;
        transition: all .5s ease-in-out;
        &:last-child {
          margin: 0;
        }
        &:hover {
          background-color: #000;
          color: #fff;
        }
      }
    }
  }
  .panner {
    margin: 0 0 22px 0;
    &:last-child {
      margin: 0;
    }
  }
}

/*------ section one end ------*/


/*------ section two start ------*/
.section-two {
  margin: 30px 0 0 0;
  .search {
    .search-header {
      font-family: ar-bold;
      font-size: 18px;
      color: rgb(3, 34, 79);
      margin: 0 20px 5px 0;
    }
    .select {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      label {
        font-family: ar-reg;
        font-size: 16px;
        color: rgb(0, 0, 0);
        padding: 13px 20px 7px 0;
        margin: 0;
      }
      .boot-select {

      }
      .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
        width: 100%;
        height: 45px;
      }
      .bootstrap-select>.dropdown-toggle {
        height: 100%;
      }
      .bootstrap-select>.dropdown-toggle.bs-placeholder, .bootstrap-select>.dropdown-toggle.bs-placeholder:active, .bootstrap-select>.dropdown-toggle.bs-placeholder:focus, .bootstrap-select>.dropdown-toggle.bs-placeholder:hover {
        border-radius: 50px;
        outline: none !important;
        padding: 0;
      }
      .bootstrap-select .dropdown-toggle .filter-option-inner-inner {
        text-align: right;
        font-family: ar-reg;
        font-size: 15px;
        color: rgb(153, 153, 153);
      }
      .bootstrap-select .dropdown-toggle .caret {
        right: unset;
        left: 30px;
      }
      .bs3.bootstrap-select .dropdown-toggle .filter-option {
        padding: 0 30px;
      }
      .bootstrap-select .dropdown-menu.inner {
        font-family: ar-reg;
        font-size: 14px;
      }
      .dropdown-menu>.active>a, .dropdown-menu>.active>a:focus, .dropdown-menu>.active>a:hover {
        background-color: #03224f;
      }
      .display {
        display: block;
      }
    }
    .search-btn {
      background-color: rgb(3, 34, 79);
      border-radius: 50px;
      width: 100%;
      height: 45px;
      font-family: ar-reg;
      font-size: 18px;
      color: rgb(255, 255, 255);
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 13px 0 0 0;
    }
  }
  .new-offers {
    .new-offers-header {
      width: 100%;
      padding: 0 0 12px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 0 30px 0;
      border-bottom: 1px solid rgb(230, 230, 230);
      h3 {
        position: relative;
        margin: 0;
        padding: 0 10px;
        font-family: ar-bold;
        font-size: 20px;
        color: rgb(3, 34, 79);
        &::after {
          content: "";
          width: 141px;
          height: 1px;
          background-color: rgb(3, 34, 79);
          position: absolute;
          bottom: -13px;
          right: 0;
        }
      }
      .show-more {
        padding: 0 10px;
        font-family: ar-reg;
        font-size: 16px;
        color: rgb(128, 128, 128);
        transition: all .3s ease-in-out;
        &:hover {
          color: #03224f;
        }
      }
    }
  }
}


/*------ section two end ------*/


/*--- card start ---*/
.cards {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .card {
    border-radius: 12px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.1);
    width: 328px;
    overflow: hidden;
    margin: 0 0 0 30px;
    &:last-child {
      margin: 0;
    }
    &:hover {
      img {
        transform: scale(1.05);
      }
    }
    .card-img {
      margin: 0 0 20px 0;
      overflow: hidden;
      img {
        transition: all .5s ease-in-out;
      }
    }
    .card-body {
      padding: 0 20px;
    }
    .card-title {
      padding: 0 0 10px 0;
      .title {
        font-family: ar-reg;
        font-size: 17px;
        color: rgb(0, 0, 0);
      }
    }
    .info {
      padding: 0 0 10px 0;
      button {
        font-size: 15px;
        color: rgb(156, 159, 166);
      }
      span {
        font-family: ar-reg;
        font-size: 14px;
        color: rgb(77, 77, 77);
        padding: 0 8px;
      }
    }
    .price {
      font-family: ar-bold;
      font-size: 20px;
      color: rgb(3, 34, 79);
      margin: 0 0 20px 0;
    }
    .show-details {
      font-family: ar-reg;
      font-size: 14px;
      color: rgb(128, 128, 128);
      display: flex;
      justify-content: flex-end;
      width: 100%;
      text-align: right;
      text-decoration: underline;
      padding: 0 0 25px 0;
      transition: all .3s ease-in-out;
      &:hover {
        color: #03224f;
      }
    }
  }
}
/*--- card end ---*/





/*------ footer start ------*/
.footer {
  margin: 120px 0 0 0;
  .zx-container {
    padding: 50px 0 0 0;
    border-top: 1px solid rgb(230, 230, 230);
  }
  .col-one {
    width: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .footer-img {
      margin: 0 0 35px 0;
    }
    ul {
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: rgb(102, 102, 102);
        text-align: center;
        transition: all 0.3s ease-in-out;
        margin: 0 0 10px 0;
        &:hover {
          color: #03224f;
        }
      }
    }
  }
  .col-two {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 0 0 16%;
    .icons {
      margin: 0 0 20px 0;
      button {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin: 0 4px;
        font-size: 20px;
        color: rgb(255, 255, 255);
        &:nth-of-type(1) {
          background-color: rgb(250, 246, 0);
        }
        &:nth-of-type(2) {
          background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
        }
        &:nth-of-type(3) {
          background-color: rgb(83, 171, 235);
        }
      }
    }
    span {
      font-family: ar-bold;
      font-size: 22px;
      color: rgb(3, 34, 79);
      display: block;
      margin: 0 0 35px 0;
    }
    p {
      font-family: ar-bold;
      font-size: 18px;
      color: rgb(3, 34, 79);
      padding: 0;
      margin: 0 0 15px 0;
    }
    .newsletter {
      position: relative;
      &::after {
        content: "\f060"; /* FontAwesome Unicode */
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 600;
        font-size: 14px;
        color: rgb(153, 153, 153);
        opacity: 0.5;
        position: absolute;
        top: 50%;
        left: 23px;
        transform: translateY(-50%);
        transition: all .3s ease-in-out;
        cursor: pointer;
      }
    }
    input {
      position: relative;
      width: 334px;
      height: 49px;
      border-radius: 50px;
      border-style: solid;
      border-width: 2px;
      border-color: rgb(237, 237, 237);
      outline: none;
      padding: 0 30px;
      &::placeholder {
        font-family: ar-reg;
        font-size: 15px;
        color: rgb(153, 153, 153);
      }
    }
  }
  .col-three {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    padding: 30px 0 0 0;
    ul {
      width: 95px;
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: rgb(102, 102, 102);
        text-align: center;
        transition: all 0.3s ease-in-out;
        margin: 0 0 10px 0;
        &:hover {
          color: #03224f;
        }
      }
    }
    .payment {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      margin: 50px 0 0 0;
      span {
        font-family: ar-bold;
        font-size: 20px;
        color: rgb(3, 34, 79);
        padding: 0 0 15px 0;
      }
      img {
        margin: 0 auto;
      }
    }
  }
  .copyrights {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 55px 0 35px 0;
    .site-copy {
      font-family: ar-reg;
      font-size: 15px;
      color: rgb(102, 102, 102);
      margin-left: 18px;
    }
    .footer-btn {
      margin: 0 auto;
      width: 173px;
      height: 45px;
      background-color: rgb(3, 34, 79);
      color: rgb(255, 255, 255);
      font-family: ar-md;
      font-size: 18px;
      border-radius: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .tasawk-copy {
      margin-right: auto;
      font-family: ar-reg;
      font-size: 15px;
      color: rgb(102, 102, 102);
      img {
        margin: 0 5px 0 0;
      }
    }
  }
}
/*------ footer end ------*/




