.fixed-menu {
  display: none;
}


/*------ responsive ------*/
@media (min-width: 1600px) {
  .owl-carousel .owl-item img {
    width: 100%;
  }
  .container {
    width: 1440px;
  }
}
@media (max-width: 992px) {
  .nav-bar .title {
    display: none;
  }
  .nav-bar .icons .user {
    display: none;
  }
  .nav-bar .icons .menu {
    display: block;
  }
  .nav-list {
    display: none;
  }

  .fixed-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 260px;
      height: 100%;
      position: absolute;
      top: 0;
      right: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        right: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 17px;
        position: absolute;
        top: 20px;
        left: 15px;
      }
    }
    .main-list {
      margin: 35px 0px;
      transform: translateX(50%);
      transition: all .5s ease-in-out;
      .slide {
        cursor: pointer;
        position: relative;
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 8px;
          left: 10px;
          transition: all .5s ease-in-out;
        }
        &.active {
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 7px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        transform: translateX(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 9px;
          left: 15px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #03224f;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-reg;
        font-size: 16px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
    .sub-list-copy {
      padding: 5px 15px;
      display: none;
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
  }
  .fixed-menu .menu-header .lang-switcher {
    a {
      font-family: ar-reg;
      font-size: 15px;
      color: #000;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    button {
      border: none;
      outline: none;
      background-color: transparent;
      padding: 0 0 0 5px;
    }
  }


  .fixed-menu .contact-icons {
    margin: 15px 0 0 0;
    .whats-app, .phone, .email {
      width: 35px;
      height: 35px;
      border-radius: 50%;
      color: #fff;
      font-size: 15px;
      transition: all .5s ease-in-out;
      &:hover {
        background-color: #03224f;
        background-image: linear-gradient( 0deg, #03224f 0%, #03224f 100%);
      }
    }
    .whats-app {
      background-color: rgb(13, 193, 67);
    }
    .phone {
      margin: 0 5px;
      transform: rotateY(180deg);
      background-image: linear-gradient( 270deg, rgb(199,200,200) 0%, rgb(156,159,166) 100%);
    }
    .email {
      background-image: linear-gradient( 90deg, rgb(199,200,200) 0%, rgb(156,159,166) 100%);
    }
  }

  

  .section-one .sections .main-menu .sub-menu-hover {
    opacity: unset;
    visibility: unset;
    min-width: unset;
    min-height: unset;
    width: 100%;
    top: 100%;
    left: unset;
    transition: unset;
    display: none;
  }
  .section-one .sections .main-menu .sub-menu:hover .sub-menu-hover {
    opacity: unset;
    visibility: unset;
    left: unset;
  }
  
  
  .panner-1 {
    img {
      margin: 0 auto 10px auto;
    }
  }
  .section-one .main-slider .main-slider-img img {
    width: 100%;
  }
  .section-one .main-slider .owl-carousel .owl-nav {
    display: none;
  }
  .section-one .main-slider .owl-carousel .owl-dots {
    bottom: 15px;
  }
  .section-one .main-slider .btns {
    margin: 25px 0;
  }
  .section-one .panner {
    margin: 5px 0 5px 0;
    img {
      margin: 0 auto;
    }
  }
  .section-two .new-offers {
    margin: 50px 0 0 0;
  }
  .cards {
    flex-direction: column;
    .card {
      margin: 10px 0;
      &:last-child {
        margin: 10px 0;
      }
    }
  }
  .footer {
    margin: 90px 0 0 0;
    .zx-container {
      padding: 30px 0 0 0;
    }
  }
  .footer .col-one {
    margin: 0 auto;
  }
  .footer .col-two {
    margin: 30px auto;
  }
  .footer .col-three {
    justify-content: center;
    align-items: center;
    .payment {
      margin: 30px 0 0 0;
    }
  }
  .footer .copyrights {
    flex-direction: column;
  }
  .footer .copyrights .footer-btn {
    order: 1;
  }
  .footer .copyrights .site-copy {
    order: 2;
    margin: 20px auto 10px auto;
  }
  .footer .copyrights .tasawk-copy {
    order: 3;
    margin: 0 auto;
  }


